import { Component, Input, OnInit } from '@angular/core';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

import { FormBuilder, FormGroup } from '@angular/forms';

import { AliasResource } from '@core/http/alias-resource.service';
import { ResponseResolverResource } from '@core/http/response-resolver-resource.service';
import { JsonSearch } from '@core/utils/json-search.service';
import { Alias } from '@data/schema/alias';
import { Request } from '@data/schema/request';
import { Response } from '@data/schema/response';

@Component({
  selector: 'response-aliases-create',
  templateUrl: './response-aliases-create.component.html',
  styleUrls: ['./response-aliases-create.component.scss'],
})
export class ResponseAliasesCreateComponent implements OnInit {

  @Input() requestData: Request;
  @Input() responseData: Response;

  form: FormGroup;
  responseResolverValues = '';

  constructor(
    private jsonSearch: JsonSearch,
    private fb: FormBuilder,
    private aliasResource: AliasResource,
    private responseResolverResource: ResponseResolverResource,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.form = this.fb.group({
      aliasName: null,
      response_resolver: null,
    });
  }

  testResponseResolver() {
    const form = this.form.value;
    const query: string = form.response_resolver;

    const res = this.jsonSearch.search(JSON.parse(this.responseData.text), query);
    this.responseResolverValues = res.join('\n');
  }

  create() {
    const component = this.responseData;
    const form = this.form.value;
    const aliasName: string = form.aliasName;
    const query: string = form.response_resolver;
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const projectId = snapshot.queryParams.projectId;

    this.aliasResource.create({
      component_id: component.id,
      component_type: component.type,
      name: aliasName,
      project_id: projectId,
    }).subscribe((res: Alias) => {
      this.createResponseResolver(this.responseData.id, res.id, query);
    });
  }

  createResponseResolver(responseId, aliasId, query) {
    this.responseResolverResource.create(this.requestData.endpointId, {
      response_id: responseId,
      alias_id: aliasId,
      query,
    }).subscribe((res) => {

    });
  }
}
