import { Component, EventEmitter, Inject, Input, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';

import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'create-modal',
  templateUrl: './create-modal.component.html',
  styleUrls: ['./create-modal.component.scss'],
})
export class CreateModalComponent implements OnInit {
  onCreate = new EventEmitter<object>();

  form: FormGroup;

  title: string;
  fields: Array<any>;

  constructor(
    @Inject(MAT_DIALOG_DATA) private config: any,
    private dialogRef: MatDialogRef<CreateModalComponent>,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.title = this.config.title;
    this.fields = this.config.fields;

    const fieldHash = {};
    this.fields.forEach(field => {
      fieldHash[field.id] = null;
    });

    this.form = this.fb.group(fieldHash);
  }

  create() {
    const form = this.form.value;

    this.onCreate.emit(form);
    this.dialogRef.close();
  }

  cancel() {
    this.dialogRef.close();
  }
}
