import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot } from '@angular/router';

import { BehaviorSubject, combineLatest, merge } from 'rxjs';
import { map } from 'rxjs/operators';

import { Breadcrumb } from '@vex/interfaces/breadcrumb.interface';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { AliasesCreateComponent } from '@aliases/components/aliases-create/aliases-create.component';
import {
  AgentService,
  AliasResource,
  BodyParamResource,
  HeaderResource,
  QueryParamResource,
  RequestResource ,
  ResponseHeaderResource,
  ResponseResource
} from '@core/http';
import {
  Alias,
  BodyParam,
  Header,
  Project,
  QueryParam,
  Request,
  RequestComponentType,
  Response,
  ResponseHeader
} from '@data/schema';
import { LayoutConfigService } from '@layout/services/layout-config.service';
import { ProjectDataService } from '@projects/services/project-data.service';
import { CreateModalComponent, EditModalComponent } from '@shared/components/modal';

import { RequestDetailsIcons } from './services/request-details-icons.service';

@Component({
  selector: 'request-details',
  templateUrl: './request-details.component.html',
  styleUrls: ['./request-details.component.scss'],
})
export class RequestDetailsComponent implements OnInit {

  constructor(
    public icons: RequestDetailsIcons,
    public layoutConfigService: LayoutConfigService,
    private aliasResource: AliasResource,
    private bodyParamResource: BodyParamResource,
    private dialog: MatDialog,
    private headerResource: HeaderResource,
    private agentService: AgentService,
    private projectDataService: ProjectDataService,
    private queryParamResource: QueryParamResource,
    private requestResource: RequestResource,
    private responseResource: ResponseResource,
    private responseHeaderResource: ResponseHeaderResource,
    private route: ActivatedRoute
  ) { }
  @ViewChild('editResponseMoreActions') template: TemplateRef<HTMLElement>;

  projectId: Project['id'];
  request: Request;

  queryParamTitle = 'Query Params';
  queryParamColumns: TableColumn<QueryParam>[];
  queryParamData: QueryParam[] = [];

  headerTitle = 'Headers';
  headerColumns: TableColumn<Header>[];
  headerData: Header[] = [];

  bodyParamTitle = 'Body Params';
  bodyParamColumns: TableColumn<BodyParam>[];
  bodyParamData: BodyParam[] = [];

  responseTitle = 'Response';
  responseData: Response;

  responseHeaderTitle = 'Headers';
  responseHeaderColumns: TableColumn<Header>[];
  responseHeaderData: Header[] = [];
  renderPrettyResponse = true;

  componentTypes = RequestComponentType;

  crumbs: Breadcrumb[] = [];

  editResponseMoreActions: TemplateRef<any>;

  ngOnInit() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.projectId = snapshot.queryParams.project_id;
    this.request = new Request(snapshot.data.request);

    this.buildCrumbs(snapshot);

    this.responseHeaderData = snapshot.data.responseHeaders.map(responseHeader => {
      return new ResponseHeader(responseHeader);
    });

    this.responseData = snapshot.data.response.map(response => {
      try {
        const text = JSON.stringify(JSON.parse(response.text), null, 2);
        response.text = text;
      } catch (err) {
        response.isJson = false;
      }

      return new Response(response);
    })[0];

    this.headerColumns = this.buildTableColumns(true);
    this.queryParamColumns = this.buildTableColumns(true);
    this.bodyParamColumns = this.buildTableColumns(true);
    this.responseHeaderColumns = this.buildTableColumns(false);
  }

// API Access

  createOrEditAlias(component, aliasName) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    if (!component.alias) {
      this.aliasResource.create({
        component_id: component.id,
        component_type: component.type,
        name: aliasName,
        project_id: this.projectId,
      }).subscribe((res: Alias) => {
        component.alias = res;
        component.aliasName = res.name;
      });
    } else {
      this.aliasResource.update(component.alias.id, {
        name: aliasName,
        project_id: this.projectId,
      }).subscribe(res => {
        component.alias.name = aliasName;
      });
    }
  }

  removeAlias(component) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.aliasResource.destroy(component.alias.id, {
      project_id: this.projectId,
      component_id: component.id,
      component_type: component.type,
    }).subscribe(res => {
      component.alias = null;
      component.aliasName = undefined;
    });
  }

  createComponent(component, componentType) {
    const resource = this.componentTypeToResource(componentType);

    if (resource) {
      component.project_id = this.projectId;

      resource.create(this.request.id, component).subscribe(res => {
        const componentData = this.componentTypeToData(componentType);
        const clone = componentData.slice();

        switch (componentType) {
          case RequestComponentType.Header:
            clone.unshift(new Header(res));
            this.headerData = clone;
            break;
          case RequestComponentType.QueryParam:
            clone.unshift(new QueryParam(res));
            this.queryParamData = clone;
            break;
          case RequestComponentType.BodyParam:
            clone.unshift(new BodyParam(res));
            this.bodyParamData = clone;
            break;
        }
      });
    }
  }

  updateComponent(component, componentType) {
    const resource = this.componentTypeToResource(componentType);

    if (resource) {
      component.project_id = this.projectId;

      resource.update(this.request.id, component.id, component).subscribe(res => {
        const componentData = this.componentTypeToData(componentType);

        if (componentType === RequestComponentType.Response) {
          this.responseData.text = res.text;
        } else {
          const clone = componentData.slice();

          clone.forEach((data, i) => {
            if (data.id === component.id) {
              clone[i] = res;
            }
          });

          this.updateComponentData(clone, componentType);
        }
      });
    }
  }

  removeComponent(component) {
    const resource = this.componentTypeToResource(component.type);

    if (resource) {
      const queryParams = {project_id: this.projectId};

      resource.destroy(this.request.id, component.id, queryParams).subscribe(res => {
        const componentData = this.componentTypeToData(component.type);

        const clone = componentData.filter((c) => {
          return c.id !== component.id;
        });

        this.updateComponentData(clone, component.type);
      });
    }
  }

// View Access

  openAliasDialog(component: (BodyParam | QueryParam | Header)) {
    const dialogRef = this.dialog.open(AliasesCreateComponent, {
      width: '600px',
      data: component || {},
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
      const request_id = snapshot.params.request_id;

      this.createOrEditAlias(component, $event.name);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

 /**
  *
  * @param {RequestComponentType} componentType enum value representing component type
  *
  */
  openCreateComponentDialog(componentType: number) {
    const dialogRef = this.dialog.open(CreateModalComponent, {
      width: '600px',
      data: this.buildComponentFields(componentType),
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe((component) => {
      this.createComponent(component, componentType);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

 /**
  *
  * @param {RequestComponentType} componentType enum value representing component type
  *
  */
  openEditComponentDialog(component: any, moreActions?: TemplateRef<any>) {
    const data = this.buildComponentFields(component.type);
    data.data = component;

    const dialogRef = this.dialog.open(EditModalComponent, {
      data,
      width: '750px',
    });

    if (moreActions) {
      const instance: EditModalComponent = dialogRef.componentInstance;
      instance.moreActions = moreActions;
    }

    const onCreateSub = dialogRef.componentInstance.onEdit.subscribe((updatedComponent) => {
      updatedComponent.id = component.id;
      this.updateComponent(updatedComponent, component.type);
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  openEditResponseLatencyDialog() {
    const data: any = {
      title: 'Latency',
      fields: [
        {
          id: 'latency',
          label: 'Latency (ms)',
          type: 'input',
        },
      ],
    };

    data.data = this.request;

    const dialogRef = this.dialog.open(EditModalComponent, {
      width: '600px',
      data,
    });

    const onCreateSub = dialogRef.componentInstance.onEdit.subscribe((updatedRequest) => {
      this.requestResource.update(
        this.request.id, { latency: updatedRequest.latency }
      ).subscribe(res => {
        this.request.latency = updatedRequest.latency;
      });
    });

    dialogRef.afterClosed().subscribe(() => {
      onCreateSub.unsubscribe();
    });
  }

  handleTabChange($event) {
    const defaultQueryParams = { project_id: this.request.projectId };

    switch ($event.tab.textLabel) {
      case this.headerTitle:
        this.headerResource.index(this.request.id, defaultQueryParams).subscribe((res: Array<any>) => {
          this.headerData = res.map(header => {
            return new Header(header);
          });
        });
        break;
      case this.queryParamTitle:
        this.queryParamResource.index(this.request.id, defaultQueryParams).subscribe((res: Array<any>) => {
          this.queryParamData = res.map(queryParam => {
            return new QueryParam(queryParam);
          });
        });
        break;
      case this.bodyParamTitle:
        this.bodyParamResource.index(this.request.id, defaultQueryParams).subscribe((res: Array<any>) => {
          this.bodyParamData = res.map(bodyParam => {
            return new BodyParam(bodyParam);
          });
        });
        break;
      case this.responseTitle:
        break;
    }
  }

  sendRequest() {
    const defaultQueryParams = { project_id: this.request.projectId };

    const headers = {};
    this.requestResource.show(this.request.id, {
      headers: 1,
      queryParams: 1,
    }).subscribe((res) => {
      this.agentService.sendRequest(
        new Request(res)
      ).subscribe(res => {
        console.log(res);
      });
    });
  }

  toggleRenderPrettyResponse(event) {
    this.renderPrettyResponse = !this.renderPrettyResponse;
  }

// Helpers

  buildCrumbs(snapshot) {
    if (this.projectDataService.project) {
      this.crumbs.push({
        name: this.projectDataService.project.name,
      });
    } else {
      const o = this.projectDataService.project$.subscribe((project: Project) => {
        if (project) {
          this.crumbs.unshift({
            name: project.name,
          });

          o.unsubscribe();
        }
      });
      this.projectDataService.fetch(this.projectId);
    }

    const pathSegments = location.pathname.split('/');
    pathSegments.shift(); // Get rid of empty first element

    this.crumbs.push({
      name: this.capitalize(pathSegments[0]),
      routerLink: ['/' + pathSegments[0]],
      queryParams: snapshot.queryParams,
    });

    // If referer URL was /endpoints or /requests
    if (pathSegments.length > 2) {
      this.addParentResourcePath(pathSegments);
    }

    this.crumbs.push({
      name: this.request.url,
    });
  }

  singularize(s: string) {
    if (s[s.length - 1] !== 's') { return s; }
    return s.substring(0, s.length - 1);
  }

  capitalize(s: string) {
    return s.charAt(0).toUpperCase() + s.slice(1);
  }

  addParentResourcePath(pathSegments) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const parentResource = snapshot.data.parentResource;
    const routerLink: string = pathSegments.slice(0, pathSegments.length / 2).join('/');

    this.crumbs.push({
      name: parentResource.name || parentResource.path,
      routerLink: ['/' + routerLink],
      queryParams: this.route.snapshot.queryParams,
    });
  }

  componentTypeToResource(componentType) {
    let resource;
    switch (componentType) {
      case RequestComponentType.Header:
        resource = this.headerResource;
        break;
      case RequestComponentType.QueryParam:
        resource = this.queryParamResource;
        break;
      case RequestComponentType.BodyParam:
        resource = this.bodyParamResource;
        break;
      case RequestComponentType.Response:
        resource = this.responseResource;
        break;
      case RequestComponentType.ResponseHeader:
        resource = this.responseHeaderResource;
        break;
    }
    return resource;
  }

  componentTypeToData(componentType) {
    let resource;
    switch (componentType) {
      case RequestComponentType.Header:
        return this.headerData;
        break;
      case RequestComponentType.QueryParam:
        return this.queryParamData;
        break;
      case RequestComponentType.BodyParam:
        return this.bodyParamData;
        break;
      case RequestComponentType.Response:
        return this.responseData;
        break;
      case RequestComponentType.ResponseHeader:
        return this.responseHeaderData;
        break;
    }
    return resource;
  }

  buildComponentFields(componentType: number): any {
    let title = '';

    switch (componentType) {
      case RequestComponentType.Header:
        title = 'Request Header';
        break;
      case RequestComponentType.QueryParam:
        title = 'Query Param';
        break;
      case RequestComponentType.BodyParam:
        title = 'Body Param';
        break;
      case RequestComponentType.Response:
        title = 'Response';
        return {
          title,
          fields: [
            {
              id: 'text',
              label: 'Body',
              type: 'textarea',
            },
          ],
        };
        break;
      case RequestComponentType.ResponseHeader:
        title = 'Response Heaer';
        break;
    }

    return {
      title,
      fields: [
        {
          id: 'name',
          label: 'Name',
          type: 'input',
        },
        {
          id: 'value',
          label: 'Value',
          rows: 3,
          type: 'textarea',
        },
      ],
    };
  }

  updateComponentData(clone, componentType) {
    switch (componentType) {
      case RequestComponentType.Header:
        this.headerData = clone;
        break;
      case RequestComponentType.QueryParam:
        this.queryParamData = clone;
        break;
      case RequestComponentType.BodyParam:
        this.bodyParamData = clone;
        break;
      case RequestComponentType.ResponseHeader:
        this.responseHeaderData = clone;
        break;
    }
  }

  buildTableColumns(canAlias: boolean): TableColumn<Header>[] {
    if (canAlias) {
      return [
        { label: 'NAME', property: 'name', type: 'text' },
        // { label: '', property: 'link-button', type: 'button', visible: true },
        { label: 'ALIAS', property: 'aliasName', type: 'custom', visible: true },
        { label: 'VALUE', property: 'value', type: 'text' },
        { label: '', property: 'edit', type: 'custom', visible: true },
      ];
    } else {
      return [
        { label: 'NAME', property: 'name', type: 'text' },
        { label: 'VALUE', property: 'value', type: 'text' },
        { label: '', property: 'edit', type: 'custom', visible: true },
      ];
    }
  }
}
