import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { tap } from 'rxjs/operators';

import { ProjectResource } from '@core/http/project-resource.service';
import { ProjectResponse } from '@data/interfaces/project-response';
import { Project } from '@data/schema/project';
import { ProjectDataService } from '@projects/services/project-data.service';

@Injectable({
  providedIn: 'root',
})
export class ProjectResolver implements Resolve<any> {

  constructor(
    private projectResource: ProjectResource,
    private projectDataService: ProjectDataService
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    const projectId = route.queryParams.project_id || route.params.project_id || route.parent.params.project_id;

    return this.projectResource.show(projectId).pipe(
      tap((project: ProjectResponse) => {
        this.projectDataService.set(new Project(project));
      })
    );
  }
}
