import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

import icAdd from '@iconify/icons-ic/twotone-add';
import icClose from '@iconify/icons-ic/twotone-close';
import icCloudDownload from '@iconify/icons-ic/twotone-cloud-download';
import icCreate from '@iconify/icons-ic/twotone-create';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icEdit from '@iconify/icons-ic/twotone-edit';
import icLink from '@iconify/icons-ic/twotone-link';
import icMoreHoriz from '@iconify/icons-ic/twotone-more-horiz';

import { TableColumn } from '@vex/interfaces/table-column.interface';
import theme from '@vex/utils/tailwindcss';

@Component({
  selector: 'request-data-table',
  templateUrl: './request-data-table.component.html',
})
export class RequestDataTableComponent<T> implements OnInit, OnChanges, AfterViewInit {
  @Input() componentType: number;
  @Input() title: string;
  @Input() data: T[];
  @Input() columns: TableColumn<T>[];
  @Input() pageSize = 10;
  @Input() editable = true;

  @Output() createOrEditAlias = new EventEmitter<any>();
  @Output() createComponent = new EventEmitter<number>();
  @Output() editComponent = new EventEmitter<number>();
  @Output() removeAlias = new EventEmitter<any>();
  @Output() removeComponent = new EventEmitter<any>();

  visibleColumns: Array<keyof T | string>;
  dataSource = new MatTableDataSource<T>();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  icMoreHoriz = icMoreHoriz;
  icCloudDownload = icCloudDownload;
  icLink = icLink;
  icDelete = icDelete;
  icCreate = icCreate;
  icClose = icClose;
  icAdd = icAdd;
  icEdit = icEdit;

  theme = theme;

  constructor() { }

  ngOnInit() { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.columns) {
      this.visibleColumns = changes.columns.currentValue.map(column => column.property);
    }

    if (changes.data && changes.data.currentValue) {
      this.dataSource.data = changes.data.currentValue;
    }
  }

  shouldRenderPre(content) {
    if (content === undefined) {
      return false;
    }

    if (typeof content !== 'string') {
      return false;
    }

    return content.indexOf('\n') !== -1;
  }

  renderText(text) {
    if (text === undefined || text === null) {
      return 'N/A';
    }

    return text.length > 80 ? (text.slice(0, 80) + '...') : text;
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
