import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class JsonSearch {

  constructor() { }

  search(data: object, query: string) {
    const res = [];
    this.searchHelper(data, query.split('.'), res);
    return res;
  }

  searchHelper(data: object, query: Array<string>, res: Array<any>) {
    if (!query.length) { return; }
    if (typeof data !== 'object') { return; }

    // Jump over arrays
    if (Array.isArray(data)) {
      for (const ele of data) {
        const clone: Array<string> = query.slice(0);
        this.searchHelper(ele, clone, res);
      }
    } else {
      data = data[query[0]];

      if (query.length === 1) {
        res.push(data);
      }

      query.pop();

      this.searchHelper(data, query, res);
    }
  }
}
