import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { ResponseResource } from '@core/http/response-resource.service';

@Injectable({
  providedIn: 'root',
})
export class ResponseResolver {
  constructor(
    private responseResource: ResponseResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.responseResource.index(
      route.params.request_id,
      {project_id: route.queryParams.project_id}
    );
  }
}
