import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { ResponseHeaderResource } from '@core/http/response-header-resource.service';

@Injectable({
  providedIn: 'root',
})
export class ResponseHeadersResolver {
  constructor(
    private responseHeaderResource: ResponseHeaderResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.responseHeaderResource.index(
      route.params.request_id,
      {project_id: route.queryParams.project_id}
    );
  }
}
