import { Component, EventEmitter, Inject, Input, OnInit, TemplateRef } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';

import { FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'edit-modal',
  templateUrl: './edit-modal.component.html',
  styleUrls: ['./edit-modal.component.scss'],
})
export class EditModalComponent implements OnInit {
  @Input() moreActions: TemplateRef<any>;

  onEdit = new EventEmitter<object>();

  form: FormGroup;

  data: any;
  title: string;
  fields: Array<any>;

  constructor(
    @Inject(MAT_DIALOG_DATA) private config: any,
    private dialogRef: MatDialogRef<EditModalComponent>,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.title = this.config.title;
    this.fields = this.config.fields;
    this.data = this.config.data || {};

    const fieldHash = {};
    this.fields.forEach(field => {
      fieldHash[field.id] = this.data[field.id];
    });

    this.form = this.fb.group(fieldHash);
  }

  update() {
    const form = this.form.value;

    this.onEdit.emit(form);
    this.dialogRef.close();
  }

  cancel() {
    this.dialogRef.close();
  }
}
